.popup-overlay {
    background: #000000b3;
    height: 100vh;
    left: 0;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 20000000;
  }
  
  .popup-content {
    background: #efefef;
    border-radius: 12vw;
    height: 74vh;
    overflow-y: auto;
    padding: 2vw;
    position: absolute;
    top: 15vh;
    left: 8vw;
    width: 80vw;
    overflow: hidden;
  }
  
  .XBtn {
    position: absolute;
    top: 4vw;
    left: 2vw;
    background: none;
    border: none;
    width: 9vw;
    z-index: 2000;
  }
  
  .popup-close-button {
    position: absolute;
    top: 5vw;
    left: 3vw;
    background: none;
    border: none;
    font-size: 8vw;
    cursor: pointer;
    width: 13vw;
    height: 9vh;
    border: none;
  }
  
  .popup-title {
    font-family: 'Karantina';
    font-weight: normal;
    color: #595959;
    font-size: 15vw;
    margin-bottom: 7vw;
    margin-top: 16vw;
    margin-left: 4vw;
    margin-right: 4vw;
    text-align: center;
  
  }
  
  .popup-body {
    font-size: 4.5vw;
    direction: ltr;
    margin-right: 1.5vh;
    text-align: right;
    margin-left: 1.5vh;
    height: 40vh;
    margin-bottom: 1vh;
    overflow: auto;
    width: 74.5vw;
  }
  
  .popup-body-text {
    direction: rtl;
  }
  
  /* width */
  .popup-body ::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Handle */
  .popup-body ::-webkit-scrollbar-thumb {
    background: #e69138;
  }
  
  .small-text{
    position: absolute;
    top: 123vw;
    left: 8vw;
    font-size: 2vh;
    color: #595959;
  }