.DiagramStep3Mahoz{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    background-image: url('/public/assets/imgs/Bg2.png');
    overflow-y: scroll; /* Ensure the container is scrollable */
    overflow-x: hidden;
}

.DiagramStep3Mahoz::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 94%;
    background: rgba(0, 0, 0, 0.5);
     backdrop-filter: blur(1px);    /*  */
    -webkit-backdrop-filter: blur(1px); /*   */
    z-index: -1;
  }
 


